import axios from 'axios';

// --- CONFIGURATION DE BASE DE L'API ---
// Dfinit l'URL de base et ajoute le prfixe '/api'
// L'URL de base sera : http://localhost:3000/api
const BASE_URL = (process.env.REACT_APP_API_URL || 'http://localhost:3000') + '/api';

// Cration de l'instance Axios
const client = axios.create({
  baseURL: BASE_URL,
  withCredentials: false, //  maintenir  false si vous utilisez des tokens (JWT) plutt que des cookies
  headers: { 
    'Content-Type': 'application/json' 
  }
});

// --- INTERCEPTEUR DE REQUTE : GESTION DU TOKEN JWT ---
// Ajoute le token d'accs (Bearer Token)  chaque requte sortante
client.interceptors.request.use((config) => {
  // Tente de rcuprer le token depuis localStorage (si 'remember me' est coch) ou sessionStorage
  const token = localStorage.getItem('token') || sessionStorage.getItem('token');
  
  if (token) {
    // Si un token est trouv, il est ajout  l'en-tte Authorization
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
}, (error) => {
  return Promise.reject(error);
});


// --- ENDPOINTS D'AUTHENTIFICATION (/auth) ---
export const auth = {
  /**
   * Envoie les identifiants pour se connecter et reoit le JWT.
   * Route: POST /auth/login
   * @param {object} payload - { email, password }
   */
  login: (payload) => client.post('/auth/login', payload),

  /**
   * Enregistre un nouvel utilisateur.
   * Route: POST /auth/register
   * @param {object} payload - Les donnes d'enregistrement de l'utilisateur
   */
  register: (payload) => client.post('/auth/register', payload),

  /**
   * Rcupre le profil de l'utilisateur authentifi (ncessite le token).
   * Route: GET /auth/me
   */
  getProfile: () => client.get('/auth/me'),

  /**
   * Dconnecte l'utilisateur.
   * Route: POST /auth/logout
   */
  logout: () => client.post('/auth/logout')
};

// --- ENDPOINTS DES EMPLOYS/UTILISATEURS (/users) ---
// Note: Utilise la ressource '/users' pour correspondre  votre UserController NestJS.
export const employees = {
  /**
   * Rcupre la liste de tous les utilisateurs.
   * Route: GET /users
   * @param {object} params - Paramtres de requte (ex: { page: 1, limit: 10 })
   */
  list: (params) => client.get('/users', { params }),

  /**
   * Rcupre un seul utilisateur par son ID.
   * Route: GET /users/:id
   * @param {number} id - L'ID de l'utilisateur
   */
  get: (id) => client.get(`/users/${id}`),

  /**
   * Cre un nouvel utilisateur.
   * Route: POST /users
   * @param {object} payload - Les donnes du nouvel utilisateur (CreateUserDto)
   */
  create: (payload) => client.post('/users', payload),

  /**
   * Met  jour partiellement un utilisateur existant.
   * Route: PATCH /users/:id (Utilise PATCH pour correspondre  votre UserController)
   * @param {number} id - L'ID de l'utilisateur  mettre  jour
   * @param {object} payload - Les donnes de mise  jour (UpdateUserDto)
   */
  update: (id, payload) => client.patch(`/users/${id}`, payload),

  /**
   * Supprime (soft-delete) un utilisateur par son ID.
   * Route: DELETE /users/:id
   * @param {number} id - L'ID de l'utilisateur  supprimer
   */
  remove: (id) => client.delete(`/users/${id}`)
};

// Exporte l'instance client Axios par dfaut
export default client;